import axios from 'axios' // node_modulesaxios
import qs from 'qs'

axios.defaults.baseURL = "http://106.52.12.54:9999/"; //  ip:port
//"http://106.52.12.54:9999/"
axios.interceptors.request.use((request) => {
    //
    var token = JSON.parse(localStorage.getItem('TOKEN'));
    if (token) {
        token = "TOKEN " + token;
        request.headers.common['Authorization'] = token;
    }
    if(request.method  === 'post'){
        let t=request.headers.patch['Content-Type']
        if(t=='application/x-www-form-urlencoded'){
          request.data = qs.stringify(request.data);
        }
    }
    return request;
},(error) =>{
    console.log('');
    return Promise.reject(error);
});

export default axios // axios




