<template>
<Form ref="formItem" :model="formItem" :rules="ruleValidate" :label-width="100" style="width:500px">
        <FormItem label="" prop="name">
            <Input v-model="formItem.name" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="author">
            <Input v-model="formItem.author" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="price">
            <Input v-model="formItem.price" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="classfication">
            <Select v-model="formItem.classfication" placeholder="">
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
            </Select>
        </FormItem>
        <FormItem label="" prop="description">
            <Input v-model="formItem.description" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="fileimage">
         <input v-if="reloadImage" type="file" placeholder="" @change="handleUploadImage" accept="image/*"></input>
        </FormItem >
        <FormItem label="" props="filepdf">
           <input v-if="reloadpdf" type="file" placeholder="" @change="handleUploadPDF" ></input>
        </FormItem>
        <FormItem>
            <Button type="primary" @click="handleSubmit('formItem')"></Button>
            <Button @click="handleReset('formItem')" style="margin-left: 8px"></Button>
        </FormItem>
        
    </Form>
</template>
<script>
    export default {
      name:"AddBook",
     
        data () {
           const checkExistBook=(rule,value,callback)=>{
             this.$api.api_all.checkExistBook(this.formItem.name
             ).then((response)=>{
               let res=response.data.res
               if(res){
                 callback(new Error(","))
               }
                else{
                   callback()
                }
             })
           }
            const validateUpload = (rule, value, callback) => {
               if (this.formItem.fileImage == null) {
                 callback(new Error(''))
              }
               else {
                   callback()
               }
             }
             const validateUploadpdf=(rule, value, callback) => {
               if(this.formItem.filepdf==null){
                 callback(new Error('pdf'))
               }
               else{
                 callback()
               }
             }
            const checkPrice=(rule, value, callback) => {
              let d=Number(this.formItem.price)
                if(isNaN(d)){
                callback(new Error(""))
                this.formItem.price=null
              }
              else{
                callback()
              }
            }
            return {
              reloadImage:true,
              reloadpdf:true,
                formItem: {
                    name:'',
                    author:'',
                    price:'',
                    classfication:'',
                    description:'',
                    fileImage:null,
                    filepdf:null
                },
                ruleValidate: {
                    name: [
                        { required: true, message: '', trigger: 'blur' },
                        //  {validator:checkExistBook,trigger:'blur'}
                    ],
                    author:[
                      {required:true,message:'',trigger:'blur'}
                    ],
                    price:[
                      {required:true,message:'',trigger:'blur'},
                      {validator:checkPrice,trigger:'blur'}
                    ],
                    classfication:[
                      {required:true,message:'',trigger:'blur'}
                    ],
                    description:[
                      {required:true,message:'',trigger:'blur'}
                    ],
                    fileimage:[
                      {validator:validateUpload,trigger:'blur'}
                    ],
                    filepdf:[
                      {validator:validateUploadpdf,trigger:'blur'}
                    ]
                }
            }
        },
        methods: {
            handleSubmit (name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                          var formData = new FormData();
                          formData.append('bookname',this.formItem.name)
                          formData.append("author",this.formItem.author)
                          formData.append("price",this.formItem.price)
                          formData.append("classfication",this.formItem.classfication)
                          formData.append("description",this.formItem.description)
                          formData.append("ImgFile",this.formItem.fileImage)
                          formData.append("PdfFile",this.formItem.filepdf)
                          const xhr = new XMLHttpRequest()
                          
                          xhr.open('POST','http://106.52.12.54:9999/UploadFile',true)
                          xhr.send(formData)
                    } else {
                        this.$Message.error('!');
                    }
                })
            },
            handleReset (name) {
                this.$refs[name].resetFields();
                this.formItem.fileImage=null
                this.formItem.filepdf=null
                 this.reloadImage=false
                    this.reloadpdf=false
                     this.$nextTick(()=>{
                       this.reloadImage=true
                       this.reloadpdf=true
                     })
            },
    
           handleUploadImage(event){
              this.formItem.fileImage=event.target.files[0]
              let name=this.formItem.fileImage.name
              let suffix=name.substring(name.lastIndexOf('.'))
              if(suffix=='.png'||suffix=='.jpg'||suffix=='.jpeg'){
              this.$Notice.success({
                title:"",
                desc:""
              })
              }
              else{
                this.$Notice.error({
                title:"",
                desc:",jpg,pngjpeg"
              })
              this.formItem.fileImage=null
              this.reloadImage=false
              this.$nextTick(()=>{
                this.reloadImage=true
              })
              }
           },
           handleUploadPDF(event){
               this.formItem.filepdf=event.target.files[0]
              let name=this.formItem.filepdf.name
              let suffix=name.substring(name.lastIndexOf('.'))
              if(suffix=='.pdf'){
              this.$Notice.success({
                title:"",
                desc:""
              })
              }
              else{
                 this.$Notice.error({
                title:"",
                desc:",pdf"
              })
              this.formItem.filepdf=null
              this.reloadpdf=false
               this.$nextTick(()=>{
                this.reloadpdf=true
              })
              }
           },
        }
    }
</script>


