<template>
	<Form ref="loginForm" :model="form" :rules="ruleslogin" @keydown.enter.native="handleSubmit">
		<FormItem prop="username">
			<Input v-model="form.username" placeholder="">
				<span slot="prepend">
				<Icon :size="16" type="ios-person"></Icon>
				</span>
			</Input>
		</FormItem>
		<FormItem prop="password">
			<Input ref="in_password" :type="this.visiable==0?'password':'text'" v-model="form.password" placeholder="">
				<span slot="prepend">
				<Icon :size="14" type="md-lock"></Icon>
				</span>
				<span slot="append">
					<Icon :size="14" :type="eye==0?'md-eye-off':'md-eye'" @click.native="handleIcon"></Icon>
				</span>
			</Input>
		</FormItem>
		<FormItem>
			<Button @click="handleSubmit" :loading="loading" type="primary" long>
				 <span v-if="!loading"></span>
                  <span v-else>...</span>
			</Button>
		</FormItem>
	</Form>
</template>

<script>
export default {
	name: 'LoginForm',

	data () {
			const checkExist=(rule,value,callback)=>{
			   this.$api.api_all.post_user_login_api(
                    this.form.username,this.form.password
                ).then((response)=>{
                    let d=response.data.flag
                    if(d==0)
                       callback(new Error(""))
                    else 
                       callback()
				}).catch((error)=>{
                    console,log(error.data.message)
				})};

		return {
			flag:0,
			eye:0,
			visiable:0,
			loading:false,
			form: {
                username: '',
				password: '',
			},
            ruleslogin:{ // 
                username: [ // FormItem prop 
					{ required: true, message: '', trigger: 'blur' },
					{validator:checkExist,trigger:'blur'}
                ],
                password: [ // FormItem prop 
                    { required: true, message: '', trigger: 'blur' },
                    { type: 'string', min: 6, message: '6', trigger: 'blur' }
                ]
            }
		}
	},
	methods: {
		handleIcon(){
			this.visiable=!this.visiable
			this.eye=!this.eye
		},
		handleSubmit () {
			this.loading=true;
			this.$refs.loginForm.validate((valid) => {
			    // this.$refs.loginForm.validate : ; -> validTrue; -> validFalse;
					this.$emit('on-success-valid', {
                        username: this.form.username, // this.form.usernamethis.form.passwordon-success-valid
						password: this.form.password,
						valid:valid}
					)

				}
			)
		},

	}
}
</script>



